}

.rectangle {
  width: 35px;
  height: 2px;
  background: #fff;
  border-radius: 2px;
  transition: left 2s;
  position: absolute;
  top: 396px;
  left: 20px;
}

.header__title {
  margin-right: 0;
  font-size: 60px;
  font-weight: 700;
  line-height: 60px;
  transition: margin-right 2s;
}

.nav__list {
  justify-content: space-between;
  padding-left: 0;
  transition: justify-content 2s;
  display: flex;
}

.nav__item {
  border-radius: 8px;
  padding: 8px 24px;
  list-style-type: none;
  transition: background-color .5s, padding 2s;
}

.nav__item:hover {
  background-color: #323243;
}

.nav__link {
  text-transform: uppercase;
  color: #fff;
  text-decoration: none;
}

.burger {
  width: 18px;
  height: 18px;
  cursor: pointer;
  display: none;
  position: absolute;
  top: 56px;
  right: 23px;
}

.burger__icon, .burger__icon:before, .burger__icon:after {
  width: 100%;
  height: 3px;
  z-index: 1;
  background: #fff;
  border-radius: 3px;
  transition: all .3s;
  display: block;
  position: absolute;
}

.burger__icon:before {
  content: "";
  transition: top .3s .3s, transform .3s cubic-bezier(.23, 1, .32, 1);
  top: -9px;
}

.burger__icon:after {
  content: "";
  transition: bottom .3s .3s, transform .3s cubic-bezier(.23, 1, .32, 1);
  bottom: -9px;
}

.burger__icon.active:before {
  content: "";
  transition: top .3s, transform .3s cubic-bezier(.68, -.55, .27, 1.55) .3s;
  top: 0;
  transform: rotate(45deg);
}

.burger__icon.active:after {
  content: "";
  transition: bottom .3s, transform .3s cubic-bezier(.68, -.55, .27, 1.55) .3s;
  bottom: 0;
  transform: rotate(-45deg);
}

.burger__icon.active {
  background: #fff0;
}

.burger__container {
  padding-left: 21px;
}

.burger__menu {
  width: 100%;
  height: 100vh;
  color: #ccc;
  z-index: -1;
  background: #0075ff;
  padding: 10px;
  transition: left 1s cubic-bezier(.68, -.55, .27, 1.55), z-index 1s cubic-bezier(.68, -.55, .27, 1.55);
  display: none;
  position: fixed;
  top: 0;
  left: -1000px;
}

.burger__menu.animate {
  z-index: 0;
  left: 0;
}

.burger__logo-small {
  justify-content: center;
  margin-top: 24px;
  margin-bottom: 48px;
  display: flex;
}

.burger__nav-list {
  margin: 0;
